.main {
	background-color: #E9E9E9;
	flex-grow: 1;
	padding: 51px 40px;
}
.main_centered {
	display: flex;
	justify-content: center;
}

.panel {
	display: grid;
	grid-template-columns: 1fr 1fr;
	gap: 20px;
}

.annotation__title {
	font-style: normal;
	font-weight: 700;
	font-size: 16px;
	line-height: 22px;
	margin: 0;
	margin-bottom: 16px;
}

.reviews {
	margin-top: 40px;
	display: flex;
	flex-direction: column;
	gap: 16px;
}


@media screen and (max-width: 800px) {
	.main {
		padding: 25px 20px;
	}
	.panel {
		grid-template-columns: auto;
		grid-template-rows: auto auto;
	}
}
